<template>
  <WebGISContainer
    :settings="settings"
  />
</template>

<script>
// Import App components
import 'css/style.css'
import WebGISContainer from './components/WebGISContainer.vue'

// Import OL modules
import Stamen from 'ol/source/Stamen';
import WMSCapabilities from 'ol/format/WMSCapabilities';
import {Group as LayerGroup, Tile as TileLayer} from 'ol/layer';
import TileWMS from 'ol/source/TileWMS';

// Handle request
import axios from "axios"

// XML parser
const parser = new WMSCapabilities();

// Settings
import {settings} from '@/assets/js/settings'

// Parsing
import {getLayersWMS, organizeLayers} from '@/assets/js/requestLayers'

export default {
  name: 'App',
  data () {
    return {
      settings: settings,
	  layer_list: {},
		}
	},
	components: {
		WebGISContainer,
	},
	async created() {
		const getLayersInfo = await getLayersWMS(this.settings.urlWMSget)
		// const organizedLayers = organizeLayers(getLayersInfo, settings.typology)
		axios.get(this.settings.urlWMSget)
            .then(response => {
                let capabilities = parser.read(response.data)
                let layers = capabilities.Capability.Layer.Layer
                let group = organizeLayers(layers)
                this.layer_list = group
				console.log(this.layer_list);
            })
            .catch(err => console.log(err))
	},
}
</script>

<style scoped>
  img {
    width: 5em;
  }
</style>