            <div class="static-layers active" id="staticLayers">
                <div class="static-layer layer-list" v-for="(layer,key,index) in layer_list.static" :key="layer.static">
                    <div class="layer-items">
                        <div v-bind:class="'chckbx-layer layer-static-'+index"><input type="checkbox" name="" id=""></div>
                        <div class="title-layer"><p v-bind:title="key">{{key}}</p></div>
                        <div class="dwnld-layer"><i class="bi bi-box-arrow-down"></i></div>
                        <div class="info-layer"><i class="bi bi-info-square"></i></div>
                        <div  class="opct-layer"><i class="bi bi-circle-half"></i></div>
                        <div class="opct-range-layer"><input type="range" name="" id=""></div>
                    </div>
                </div>
            </div>
            <h6 class="time-series-layers-title"><i class="bi bi-arrow-right"></i>Time Series Layers</h6>
            <div class="time-series-layers active" id="timeSeriesLayers">
                <div class="time-series-layer layer-list"  v-for="(layer, key, index) in layer_list.timeSeries" :key="layer.timeSeries">
                    <div class="layer-items">
                        <div v-bind:class="'chckbx-layer layer-timeSeries-'+index"><input type="checkbox" name="" id=""></div>
                        <div class="title-layer"><p v-bind:title="key">{{key}}</p></div>
                        <div class="dwnld-layer"><i class="bi bi-box-arrow-down"></i></div>
                        <div class="info-layer"><i class="bi bi-info-square"></i></div>
                        <div  class="opct-layer"><i class="bi bi-circle-half"></i></div>
                        <div class="opct-range-layer"><input type="range" name="" id=""></div>
                    </div>
                </div>
            </div>
        </div>
</template>

<script>
export default {
    name: 'Layer',
	props:{
		child_msg: {
			type: String,
			default: 'Layer Panel',
		},
        layer_list: {
            type: Object,
        }
	},
    data () {
        
    },
    mounted() {

    }
}
</script>

<style lang="scss" scoped>
@import "@/assets/sass/style.scss";

.layer-panel {
    position: absolute;
    display:none;
    top: 0.5em;
    left: 2.5em;
    width: 40vw;
    height: 50vh;
    overflow-y: auto;

    h6{
        margin:0;
        font-size: 16px;
    }
}

.layer-list{
    background: rgba($tertiary-bg-color,0.5)!important;
}

.layer-items{
    display: inline-flex;
    vertical-align: middle;
    width:98%;
    background: rgba($primary-bg-color,0.5);
    padding: 4px;
    border-radius: 4px;

    ul{
        list-style: none;
        padding: 0;
        margin:0;
    }

    .chckbx-layer{
        width: 5%;
    }

    .title-layer{
        width:35%;
        p{
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
    }

    .dwnld-layer{
        width: 5%;
    }

    .info-layer{
        width: 5%;
    }

    .opct-layer{
        width: 5%;
    }

    .opct-range-layer{
        width: 35%;
    }

    p{
        margin: 0;
    }

    input[type=range]{
        padding: 0;
        margin: 0;
