import axios from "axios"
import WMSCapabilities from 'ol/format/WMSCapabilities';
// XML parser
const parser = new WMSCapabilities();

function getLayersWMS(urlWMS) {
    axios.get(urlWMS)
    .then(response => {
        let capabilities = parser.read(response.data)
        let layers = capabilities.Capability.Layer.Layer
        let group = organizeLayers(layers)
        return group
    })
    .catch(err => console.log(err))
}

function organizeLayers(layers) {
    let group = {'static':{},'timeSeries':{}}
    layers.forEach(layer => {
        let titleSplit = layer.Title.split('_');
        let groupName = ''
        // If convention as time series hold true
        if (titleSplit.length == 5) {
            groupName = titleSplit.slice(0,3).join('_') + '_' + titleSplit[4]
            // Organize metadata
            let satellite = titleSplit[0]
            let wqp = titleSplit[1]
            let crs = titleSplit[2]
            let ts = titleSplit[3]
            let tsFormatted = ''
            // Format time depending on the satellite
            if (satellite == "L8"){
                tsFormatted = ts.substr(0,4) + '-' + ts.substr(4,2) + '-' + ts.substr(6,2)
            } else {
                tsFormatted = ts.substr(0,4) + '-' + ts.substr(4,2) + '-' + ts.substr(6,2) + ' ' + ts.substr(9,2) +":" + ts.substr(11,2) + ":" + ts.substr(13,2)
            }
            let lvl = titleSplit[4]
            // Store metadata in dictionary
            // If new element
            if (groupName in group['timeSeries']) {
                group['timeSeries'][groupName]["title"].push(layer.Title)
                group['timeSeries'][groupName]["layer"].push(layer)
                group['timeSeries'][groupName]["ts"].push(ts)
                group['timeSeries'][groupName]["tsFormatted"].push(new Date(tsFormatted))
            } else {
                group['timeSeries'][groupName] = {}
                group['timeSeries'][groupName]["title"] = [layer.Title]
                group['timeSeries'][groupName]["layer"] = [layer]
                group['timeSeries'][groupName]["satellite"] = satellite
                group['timeSeries'][groupName]["wqp"] = wqp
                group['timeSeries'][groupName]["crs"] = crs
                group['timeSeries'][groupName]["ts"] = [ts]
                group['timeSeries'][groupName]["tsFormatted"] = [new Date(tsFormatted)]
                group['timeSeries'][groupName]["lvl"] = lvl
            }
                
        } else {
            groupName = titleSplit.join('_')
            group['static'][groupName] = {"layer":layer}
        }
    });
    return group
}

export {
    getLayersWMS,
    organizeLayers
}